
.home-card-img {
  padding-bottom: 160%;
  display: block;
  position: relative;
  max-width: 100%;
  background-size: cover;
  background-position: center center;
  cursor: pointer;
  border-radius: 2px;
}
.home-card-title {
  color: #fff;
  text-shadow: 0 0 2px rgba(0, 0, 0, 0.5);
  font-weight: 500;
  font-size: 16px;
  position: absolute;
  left: 0;
  right: 0;
  width: 100%;
  text-align: center;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.home-row {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
}
.home-card {
  border-radius: 10px;
  margin: 10px;
  overflow: hidden;
  width: 50%;
  height: 50%;
  bottom: 50%;
  -webkit-box-flex: 1;
  -webkit-flex: 1;
          flex: 1;
}



